import React from "react";

{/* composant rutilasable pour l'affichage d'une bannire responsive syr les page Services, Ralisations et Blog*/}

function Bannire(props) {
  const { desktopImage, tabletImage, mobileImage, height = "25vh" } = props;

  return (
    <section className="banner-section">
      {/* Bannire Desktop */}
      <div
        className="d-none d-lg-block"
        style={{
          backgroundImage: `url(${desktopImage})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          height,
        }}
      ></div>

      {/* Bannire Tablette */}
      <div
        className="d-none d-md-block d-lg-none"
        style={{
          backgroundImage: `url(${tabletImage})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          height,
        }}
      ></div>

      {/* Bannire Mobile */}
      <div
        className="d-block d-md-none"
        style={{
          backgroundImage: `url(${mobileImage})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          height,
        }}
      ></div>
    </section>
  );
}

export default Bannire;
